@import '../../../scss/constants';
@import '../../../scss/mixins';

.card {
   display: flex;
   padding-right: 85px;

   &__avatar {
      width: 200px;
      height: 200px;
      margin-right: 20px;
      position: relative;

      >img {
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
         object-fit: contain;
      }
   }

   &__title {
      @include font($size: 2.4rem, $height: 2.8rem, $weight: 500, $text-transform: none);
      color: $color-main-dark;
   }

   &__text {
      width: 400px;
      margin-top: 27px;
      @include font($size: 1.8rem, $height: 2.1rem, $weight: 300, $text-transform: none);
      color: $color-grey;
      font-style: italic;
      //  & span {
      //     position: relative;
      //     > img {
      //        width: 1.5rem;
      //     }
      //  }
   }

   // &__open {
   //    position: absolute;
   //    top: -0.5rem;
   //    left: -1rem;
   // }

   // &__close {
   //    position: absolute;
   //    right: -3rem;
   //    bottom: -0.5rem;
   // }
}