@import '../../../scss/constants';
@import '../../../scss/mixins';

.radio__wrapper {

  .ant-radio-wrapper.ant-radio-wrapper-in-form-item.online__checkbox {
    .ant-radio.ant-wave-target {
      margin-top: -55px;
    }
  }




  .ant-radio-group.ant-radio-group-outline {
    .ant-radio-wrapper.ant-radio-wrapper-checked {



      .ant-radio.ant-wave-target {
        color: $color-primary;
        background-color: $color-primary;

        .ant-radio-inner::after {
          background-color: $color-primary;
        }
      }

      >span {
        color: $color-primary;
      }
    }

    .ant-radio-wrapper {
      .ant-radio.ant-wave-target {
        .ant-radio-inner {
          border-radius: 5px;
          border: 1px solid $color-grey;
          width: 28px;
          height: 28px;
          background-color: transparent;
        }

        .ant-radio-inner::after {
          background-color: transparent;
        }
      }
    }
  }
}

.payment__online {
  color: $color-grey;
  @include font($size: 2.4rem, $height: 2.8rem, $weight: 400, $text-transform: none);
}

.payment__cash {
  color: $color-grey;
  @include font($size: 2.4rem, $height: 2.8rem, $weight: 400, $text-transform: none);
}